<template>
<div>
<u-button @click="load">加载</u-button>
<u-loading v-if="loading" display="fullWindow">正在加载中...</u-loading>
</div>
</template>
<script>
export default {
    data() {
        return {
            loading: false,
        };
    },
    methods: {
        load() {
            this.loading = true;
            setTimeout(() => this.loading = false, 2000);
        },
    },
};
</script>



