<h2>API</h2>
<h3>Props/Attrs</h3>
<table>
<thead>
<tr>
<th>Attr/Prop</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>visible.sync</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否显示</td>
</tr>
<tr>
<td>title</td>
<td>String</td>
<td><code>'提示'</code></td>
<td>弹窗的标题</td>
</tr>
<tr>
<td>ok-button</td>
<td>String</td>
<td><code>'确定'</code></td>
<td>确定按钮文本，如果为空则不显示</td>
</tr>
<tr>
<td>cancel-button</td>
<td>String</td>
<td><code>'取消'</code></td>
<td>取消按钮文本，如果为空则不显示</td>
</tr>
<tr>
<td>size</td>
<td>String</td>
<td><code>'normal'</code></td>
<td>弹窗的尺寸。可选值：<code>'small'</code>, <code>'normal'</code>, <code>'large'</code></td>
</tr>
<tr>
<td>static</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否嵌入页面显示</td>
</tr>
<tr>
<td>mask-closable</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否点击遮罩时关闭弹窗</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>弹窗标题自定义</td>
</tr>
<tr>
<td>head</td>
<td>弹窗头部自定义</td>
</tr>
<tr>
<td>foot</td>
<td>弹窗尾部自定义</td>
</tr>
<tr>
<td>(default)</td>
<td>弹窗内容自定义</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<h4>@open</h4>
<p>打开弹窗时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>senderVM</td>
<td>UModal</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
<h4>@ok</h4>
<p>确定时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>senderVM</td>
<td>UModal</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
<h4>@cancel</h4>
<p>取消时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>senderVM</td>
<td>UModal</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
<h4>@before-close</h4>
<p>关闭弹窗前触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.ok</td>
<td>Boolean</td>
<td>是否确定</td>
</tr>
<tr>
<td>$event.preventDefault</td>
<td>Function</td>
<td>阻止关闭流程</td>
</tr>
<tr>
<td>senderVM</td>
<td>UModal</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
<h4>@close</h4>
<p>关闭弹窗时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.ok</td>
<td>Boolean</td>
<td>是否确定</td>
</tr>
<tr>
<td>senderVM</td>
<td>UModal</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
<h3>Static Methods</h3>
<h4>alert(content, title)</h4>
<p>打开只有确定按钮的弹窗</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>content</td>
<td>String</td>
<td>弹窗内容</td>
</tr>
<tr>
<td>title</td>
<td>String</td>
<td>弹窗标题</td>
</tr>
</tbody>
</table>
<h4>confirm(content, title)</h4>
<p>打开有确定和取消按钮的弹窗</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>content</td>
<td>String</td>
<td>弹窗内容</td>
</tr>
<tr>
<td>title</td>
<td>String</td>
<td>弹窗标题</td>
</tr>
</tbody>
</table>
</u-article></template>

<script>
    import Component0 from "./anondemo-6ad5f149-338.vue";
import Component1 from "./anondemo-787cfabe-497.vue";

    export default {
        components: {
'anondemo-6ad5f149-338': Component0,
'anondemo-787cfabe-497': Component1,
},
    }
</script>
