module.exports = "<template><u-article><h1>ULoading 加载中</h1>
<h2>示例</h2>
<h3>基本形式</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-loading.vue/README.md">
    <div><u-loading></u-loading></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-loading</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-loading</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>添加文字</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-loading.vue/README.md">
    <div><u-loading>正在加载中...</u-loading></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-loading</span>&gt;</span>正在加载中...<span class="hljs-tag">&lt;/<span class="hljs-name">u-loading</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>大小扩展</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-loading.vue/README.md">
    <div><u-linear-layout direction="vertical">
    <div><u-loading size="small">正在加载中...</u-loading></div>
    <div><u-loading>正在加载中...</u-loading></div>
    <div><u-loading size="large">正在加载中...</u-loading></div>
</u-linear-layout></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span> <span class="hljs-attr">direction</span>=<span class="hljs-string">"vertical"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">u-loading</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span>&gt;</span>正在加载中...<span class="hljs-tag">&lt;/<span class="hljs-name">u-loading</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">u-loading</span>&gt;</span>正在加载中...<span class="hljs-tag">&lt;/<span class="hljs-name">u-loading</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">u-loading</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span>&gt;</span>正在加载中...<span class="hljs-tag">&lt;/<span class="hljs-name">u-loading</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>展示方式</h3>
<h4>行内展示</h4>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-loading.vue/README.md">
    <div><u-loading size="small" display="inline"></u-loading> 默认在行内...</div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-loading</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">display</span>=<span class="hljs-string">"inline"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-loading</span>&gt;</span> 默认在行内...</code></pre>
</div>
</u-code-example>

<h4>块级展示</h4>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-loading.vue/README.md">
    <div><u-loading>正在加载中...</u-loading></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-loading</span>&gt;</span>正在加载中...<span class="hljs-tag">&lt;/<span class="hljs-name">u-loading</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h4>填充父元素</h4>
<p>将<code>display</code>属性设置为<code>full</code>，可以填充定位父元素（offsetParent）。</p>
<u-code-example
    :show-code="false"
    :show-detail="false"
    file-path="src/components/u-loading.vue/README.md">
    <div><u-list-view>
    <u-list-view-item>水杯</u-list-view-item>
    <u-list-view-item>坚果</u-list-view-item>
    <u-list-view-item>毛巾</u-list-view-item>
    <u-list-view-item>沙发</u-list-view-item>
    <u-loading display="full">正在加载中...</u-loading>
</u-list-view></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-list-view</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-list-view-item</span>&gt;</span>水杯<span class="hljs-tag">&lt;/<span class="hljs-name">u-list-view-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-list-view-item</span>&gt;</span>坚果<span class="hljs-tag">&lt;/<span class="hljs-name">u-list-view-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-list-view-item</span>&gt;</span>毛巾<span class="hljs-tag">&lt;/<span class="hljs-name">u-list-view-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-list-view-item</span>&gt;</span>沙发<span class="hljs-tag">&lt;/<span class="hljs-name">u-list-view-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-loading</span> <span class="hljs-attr">display</span>=<span class="hljs-string">"full"</span>&gt;</span>正在加载中...<span class="hljs-tag">&lt;/<span class="hljs-name">u-loading</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-list-view</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h4>填充全窗口</h4>
<p>将<code>display</code>属性设置为<code>fullWindow</code>，则会填满整个浏览器。</p>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="src/components/u-loading.vue/README.md">
    <div><anondemo-945f76b0-398 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"load"</span>&gt;</span>加载<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-loading</span> <span class="hljs-attr">v-if</span>=<span class="hljs-string">"loading"</span> <span class="hljs-attr">display</span>=<span class="hljs-string">"fullWindow"</span>&gt;</span>正在加载中...<span class="hljs-tag">&lt;/<span class="hljs-name">u-loading</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">loading</span>: <span class="hljs-literal">false</span>,
        };
    },
    <span class="hljs-attr">methods</span>: {
        load() {
            <span class="hljs-keyword">this</span>.loading = <span class="hljs-literal">true</span>;
            setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> <span class="hljs-keyword">this</span>.loading = <span class="hljs-literal">false</span>, <span class="hljs-number">2000</span>);
        },
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h2>API</h2>
<h3>Props/Attrs</h3>
<table>
<thead>
<tr>
<th>Prop/Attr</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>size</td>
<td>String</td>
<td><code>'normal'</code></td>
<td>图标的大小。可选值：<code>'small'</code>, <code>'normal'</code>, <code>'large'</code></td>
</tr>
<tr>
<td>display</td>
<td>String</td>
<td><code>'inline'</code></td>
<td>展示方式。可选值：<code>'inline'</code>表示按行内展示，<code>'block'</code>表示按块级展示，<code>'full'</code>表示填充定位父元素（offsetParent），<code>'fullWindow'</code>表示填充整个浏览器</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<h4>(default)</h4>
<p>插入文本或 HTML。</p>
</u-article></template>

<script>
    import Component0 from "./anondemo-945f76b0-398.vue";

    export default {
        components: {
'anondemo-945f76b0-398': Component0,
},
    }
</script>
            "


