module.exports = "<template><u-article><h1>UToast 吐司提示</h1>
<h2>示例</h2>
<h3>快捷方式</h3>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="src/components/u-toast.vue/README.md">
    <div><anondemo-5fa2768c-203 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"show"</span>&gt;</span>Toast<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        show() {
            <span class="hljs-keyword">this</span>.$toast.show(<span class="hljs-string">'This is a text.'</span>);
        },
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>位置扩展</h3>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="src/components/u-toast.vue/README.md">
    <div><anondemo-1796aaec-648 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"show('top-center')"</span>&gt;</span>Top Center<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"show('top-left')"</span>&gt;</span>Top Left<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"show('top-right')"</span>&gt;</span>Top Right<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"show('bottom-center')"</span>&gt;</span>Bottom Center<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"show('bottom-left')"</span>&gt;</span>Bottom Left<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"show('bottom-right')"</span>&gt;</span>Bottom Right<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        show(position) {
            <span class="hljs-keyword">this</span>.$toast.position = position;
            <span class="hljs-keyword">this</span>.$toast.show(<span class="hljs-string">'position: '</span> + position);
        },
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>嵌入文档流</h3>
<p>上面的 Toast 都是以<code>fixed</code>的形式固定在浏览器中，也可以将Toast嵌入文档流。只需使用<code>u-toast</code>标签，并设置<code>position=&quot;static&quot;</code>即可。</p>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-toast.vue/README.md">
    <div><p><u-button @click="$refs.toast.show()">Static</u-button></p>
<u-toast ref="toast" position="static" text="Static Toast"></u-toast></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"$refs.toast.show()"</span>&gt;</span>Static<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-toast</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"toast"</span> <span class="hljs-attr">position</span>=<span class="hljs-string">"static"</span> <span class="hljs-attr">text</span>=<span class="hljs-string">"Static Toast"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-toast</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>可关闭</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-toast.vue/README.md">
    <div><p><u-button @click="$refs.toast.show()">Static</u-button></p>
<u-toast ref="toast" position="static" text="Closable" :duration="0" closable></u-toast></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"$refs.toast.show()"</span>&gt;</span>Static<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-toast</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"toast"</span> <span class="hljs-attr">position</span>=<span class="hljs-string">"static"</span> <span class="hljs-attr">text</span>=<span class="hljs-string">"Closable"</span> <span class="hljs-attr">:duration</span>=<span class="hljs-string">"0"</span> <span class="hljs-attr">closable</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-toast</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>提示停留时间</h3>
<p>可以通过设置toast的<code>duration</code>参数设置所有提示的停留时间，也可以在<code>show</code>的时候单独设置该条提示的停留时间，单位为毫秒。</p>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="src/components/u-toast.vue/README.md">
    <div><anondemo-828bd112-421 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"show(500)"</span>&gt;</span>0.5s<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"show(1000)"</span>&gt;</span>1s<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"show(2000)"</span>&gt;</span>2s<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"show(0)"</span>&gt;</span>常驻<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        show(duration) {
            <span class="hljs-keyword">this</span>.$toast.show(<span class="hljs-string">'duration: '</span> + duration + <span class="hljs-string">' ms'</span>, duration);
        },
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>始终显示一条</h3>
<p>将<code>single</code>设置为<code>true</code>，可以让toast始终只显示一条提示。</p>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="src/components/u-toast.vue/README.md">
    <div><anondemo-bb12694c-350 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-button</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"show()"</span>&gt;</span>Single<span class="hljs-tag">&lt;/<span class="hljs-name">u-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">number</span>: <span class="hljs-number">1</span>,
        };
    },
    <span class="hljs-attr">methods</span>: {
        show() {
            <span class="hljs-keyword">this</span>.$toast.single = <span class="hljs-literal">true</span>;
            <span class="hljs-keyword">this</span>.$toast.show(<span class="hljs-string">'number: '</span> + <span class="hljs-keyword">this</span>.number);
            <span class="hljs-keyword">this</span>.number++;
        },
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h2>API</h2>
<h3>Props/Attrs</h3>
<table>
<thead>
<tr>
<th>Prop/Attr</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>position</td>
<td>String</td>
<td><code>'top-center'</code></td>
<td>显示的位置，可选值：<code>'top-center'</code>, <code>'top-left'</code>, <code>'top-right'</code>, <code>'bottom-center'</code>, <code>'bottom-left'</code>, <code>'bottom-right'</code>, <code>static</code></td>
</tr>
<tr>
<td>duration</td>
<td>Number</td>
<td><code>2000</code></td>
<td>提示停留的时间。<code>0</code>表示常驻。</td>
</tr>
<tr>
<td>single</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>多个提示会合并为一个</td>
</tr>
<tr>
<td>text</td>
<td>String</td>
<td><code>''</code></td>
<td>默认提示内容</td>
</tr>
<tr>
<td>color</td>
<td>String</td>
<td><code>'default'</code></td>
<td>提示的颜色</td>
</tr>
<tr>
<td>closable</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否可以关闭提示</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<h4>@open</h4>
<p>弹出提示时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.text</td>
<td>String</td>
<td>提示的内容</td>
</tr>
<tr>
<td>$event.color</td>
<td>String</td>
<td>提示的颜色</td>
</tr>
<tr>
<td>$event.duration</td>
<td>Number</td>
<td>提示停留的时间</td>
</tr>
<tr>
<td>senderVM</td>
<td>UToast</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
<h4>@before-close</h4>
<p>关闭提示前触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.text</td>
<td>String</td>
<td>提示的内容</td>
</tr>
<tr>
<td>$event.color</td>
<td>String</td>
<td>提示的颜色</td>
</tr>
<tr>
<td>$event.duration</td>
<td>Number</td>
<td>提示停留的时间</td>
</tr>
<tr>
<td>$event.preventDefault</td>
<td>Function</td>
<td>阻止关闭流程</td>
</tr>
<tr>
<td>senderVM</td>
<td>UToast</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
<h4>@close</h4>
<p>关闭提示时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.text</td>
<td>String</td>
<td>提示的内容</td>
</tr>
<tr>
<td>$event.color</td>
<td>String</td>
<td>提示的颜色</td>
</tr>
<tr>
<td>$event.duration</td>
<td>Number</td>
<td>提示停留的时间</td>
</tr>
<tr>
<td>senderVM</td>
<td>UToast</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
</u-article></template>

<script>
    import Component0 from "./anondemo-5fa2768c-203.vue";
import Component1 from "./anondemo-1796aaec-648.vue";
import Component2 from "./anondemo-828bd112-421.vue";
import Component3 from "./anondemo-bb12694c-350.vue";

    export default {
        components: {
'anondemo-5fa2768c-203': Component0,
'anondemo-1796aaec-648': Component1,
'anondemo-828bd112-421': Component2,
'anondemo-bb12694c-350': Component3,
},
    }
</script>
            "


